/** MultilineChart.js */
import React from "react";
import * as d3 from "d3";
// import {event as currentEvent} from 'd3-selection'
import { CCard, CCardBody } from "@coreui/react";
import { useDispatch, useSelector } from "react-redux";
import Select from 'react-select'

var json = require('./data2.json')
const Dendogram = () => {
  const dispatch = useDispatch()
  const modal = useSelector((state)=>state.modal)
  const table = json
  const svgRef = React.useRef(null)
  const margin = { top: 60, right: 120, bottom: 10, left: 0 };    
  React.useEffect(() => {
    const width = 900;
    const dx = 10;
    const dy = 300;
    const tooltip = d3.select("body").append("div")	
    .attr("class", "tooltip")				
    .style("opacity", 0);

    const tree = d3.tree().nodeSize([100, 190])
    .separation(function(a, b) {
      return a.parent == b.parent ? 1 : 1.25;
    });
    var formatTime = d3.timeFormat("%e %B");
    const diagonal = d3
      .linkHorizontal()
      .x((d) => {
        return d.y+70
      })
      .y((d) => {
        return d.x 
      });
    const root = d3.stratify()
    .id(function(d) { return d.kd_eldat; })
    .parentId(function(d) { return d.kd_parent; })
    (table)   
    root.x0 = 159 / 2;
    root.y0 = 0;
    root.descendants().forEach((d, i) => {
      d.id = i;
      d._children = d.children;
      d.children = null;
    });    
    const svg = d3
      .select(svgRef.current)
      .attr("viewBox", [-20, -margin.top, 954, 10])
      .style("font", "10px sans-serif")
      .style("user-select", "none");
    const gLink = svg
      .append("g")
      .attr("fill", "none")
      .attr("stroke", "#555")
      .attr("stroke-opacity", 0.4)
      .attr("stroke-width", 3);
    const gNode = svg
      .append("g")
      .attr("cursor", "pointer")
      .attr("pointer-events", "all");
    function update(source) {
      const duration = 250
      const nodes = root.descendants().reverse()
      // let bro = nodes.map((x)=>{
      //   return {...x, ['y']: x.depth * 180 }
      // })
      // console.log(bro)
      const links = root.links();

      // Compute the new tree layout.
      tree(root);

      let left = root;
      let right = root;
      root.eachBefore((node) => {
        if (node.x < left.x) left = node;
        if (node.x > right.x) right = node;
      });

      const height = right.x - left.x + margin.top + margin.bottom + 60;
      const width = 500 + right.y + left.y + margin.left + margin.right + 500;

      const transition = svg
        .transition()
        .duration(duration)
        .attr("width",width)
        .attr("viewBox", [-margin.left, left.x - margin.top, width, height])
        .tween(
          "resize",
          window.ResizeObserver ? null : () => () => svg.dispatch("toggle")
        );

      // Update the nodes…
      const node = gNode.selectAll("g").data(nodes, (d) => d.id);

      var rectHeight = 60, rectWidth = 140;
      const nodeEnter = node
        .enter()
        .append("g")
        .attr("transform", () => {
          return `translate(${source.y0},${source.x0})`
        })
        .attr("fill-opacity", 0)
        .attr("stroke-opacity", 0)
        .on("click", (event, d) => {
          console.log(d)
          d.children = d.children ? null : d._children;
          update(d);
        })

      nodeEnter.append('rect')
      .attr('class', 'node')
      .attr("width", function(d){
        return rectWidth
        // if(d.data.ur_eldat.length+d.data.Value.length < 19){
        //   return rectWidth
        // } else {
        //   let sisa = (d.data.ur_eldat.length + d.data.Value.length + 2 - 19)*4.9
        //   let tambah = sisa + rectWidth
        //   return tambah
        // }
      })
      .attr("height", rectHeight)
      .attr("x", 0)
      .attr("y", (rectHeight/2)*-1)
      .attr("rx","5")
      .style("box-shadow","rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px!important")
      .style("fill", function(d){
        return d.data.fill
      }) 
      .on("mouseover", function(event, d) {  
        d3.select(this.parentNode)
        .attr("transform", (d)=>{
          return `translate(${d.y0},${d.x0}), scale(1.2,1.2)`
        })                     
        d3.select(this.parentNode.childNodes[0])
        .attr("width", function(d){
          if(d.data.ur_eldat.length+d.data.Value.length < 19){
            return rectWidth
          } else {
            let sisa = (d.data.ur_eldat.length + d.data.Value.length + 2 - 19)*4.9
            let tambah = sisa + rectWidth
            return tambah
          } 
        }) 
        d3.select(this.parentNode.childNodes[1].childNodes[1])
        .text(function(d){
          if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
            return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})`            
          } else {
            return `${d.data.ur_eldat}`
          }
        }) 
        if(d.data.kd_eldat != "0"){
          // const initial = d.data.KL
          // const cleaned = initial.trim().substring(1, initial.length - 1)
          const arrKL = d.data.KL  
          // const initialijin = d.data.ur_ijin
          // const cleanedijin = initialijin.trim().substring(1, initialijin.length - 1)
          const arrIjin = d.data.ur_ijin 
          const arrDS = d.data.dok_syarat
          const arrDK = d.data.data_khusus
          const arrRekom = d.data.ur_rekom          
          var div = d3.select("body").append("div")	
          .attr("class", "tooltip")
          .attr("id", "jakasembungbawagolok")				
          .style("opacity", 0)                                             
          div.transition()		
              .duration(200)		
              .style("opacity", 1);		
          div	.html("<b>K/L Pembina RKI :</b>"+ arrKL.map((x)=>{
            return(
              "<li>"+x+"</li>"
            )
          }).join('')+"<b>Ijin : </b>"+ arrIjin.map((y)=>{
            return(
              "<li>"+y+"</li>"
            )
          }).join('')+"<b>Rekom : </b>"+ arrRekom.map((z)=>{
            return(
              "<li>"+z+"</li>"
            )
          }).join('')+"<b>Dok Syarat : </b>"+ arrDS.map((a)=>{
            return(
              "<li>"+a+"</li>"
            )
          }).join('')+"<b>Data Khusus : </b>"+ arrDK.map((b)=>{
            return(
              "<li>"+b+"</li>"
            )
          }).join(''))
            .style("right", '20px')		
            .style("bottom", '20px')          
        }                                        
      })  
      .on("mouseout", function(event, d){
        if(d.data.kd_eldat != '0'){
          var div = d3.select("#jakasembungbawagolok")
          div.remove()           
        }       
        d3.select(this.parentNode)
        .attr("transform", (d)=>{
          return `translate(${d.y0},${d.x0}), scale(1,1)`
        })                     
        d3.select(this.parentNode.childNodes[0])
        .attr("width", function(d){
          return rectWidth
        }) 
        d3.select(this.parentNode.childNodes[1].childNodes[1])
        .text(function(d){
          let jumlah = d.data.ur_eldat.length + d.data.Value.length
          if (jumlah < 26){
            if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
              return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})`              
            }  else {
              return `${d.data.ur_eldat}`              
            }
          } else {
            let copytext = d.data.ur_eldat
            let b = 26 - d.data.Value.length
            let text = copytext.slice(0,b-4) + "..."
            if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
              return `${text} (Kode : ${d.data.kd_eldat})`
            } else {
              return `${text}`              
            }
          }
        })                
      })          
      nodeEnter.append('text')
      .attr("dy", "-.35em")
      .attr("x", function(d) {
        return 13;
      })
      .attr("text-anchor", function(d) {
        return "start";
      })
      .text(function(d) { 
        return `${d.data.ur_grup}` 
      })
      .style("fill", "white")
        .on("mouseover", function(event, d) {          
          d3.select(this.parentNode)
          .attr("transform", (d)=>{
            return `translate(${d.y0},${d.x0}), scale(1.2,1.2)`
          })                     
          d3.select(this.parentNode.childNodes[0])
          .attr("width", function(d){
            if(d.data.ur_eldat.length+d.data.Value.length < 19){
              return rectWidth
            } else {
              let sisa = (d.data.ur_eldat.length + d.data.Value.length + 2 - 19)*4.9
              let tambah = sisa + rectWidth
              return tambah
            } 
          }) 
          d3.select(this.parentNode.childNodes[1].childNodes[1])
          .text(function(d){
            if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
              return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})`
            } else {
              return `${d.data.ur_eldat}`              
            }
          }) 
          if(d.data.kd_eldat != "0"){
            // const initial = d.data.KL
            // const cleaned = initial.trim().substring(1, initial.length - 1)
            const arrKL = d.data.KL
            // const initialijin = d.data.ur_ijin
            // const cleanedijin = initialijin.trim().substring(1, initialijin.length - 1)
            const arrIjin = d.data.ur_ijin 
            const arrDS = d.data.dok_syarat
            const arrDK = d.data.data_khusus
            const arrRekom = d.data.ur_rekom             
            var div = d3.select("body").append("div")	
            .attr("class", "tooltip")
            .attr("id", "jakasembungbawagolok")				
            .style("opacity", 0)                                             
            div.transition()		
                .duration(200)		
                .style("opacity", 1);		
            div	.html("<b>K/L Pembina RKI :</b>"+ arrKL.map((x)=>{
              return(
                "<li>"+x+"</li>"
              )
            }).join('')+"<b>Ijin : </b>"+ arrIjin.map((y)=>{
              return(
                "<li>"+y+"</li>"
              )
            }).join('')+"<b>Rekom : </b>"+ arrRekom.map((z)=>{
              return(
                "<li>"+z+"</li>"
              )
            }).join('')+"<b>Dok Syarat : </b>"+ arrDS.map((a)=>{
              return(
                "<li>"+a+"</li>"
              )
            }).join('')+"<b>Data Khusus : </b>"+ arrDK.map((b)=>{
              return(
                "<li>"+b+"</li>"
              )
            }).join(''))
              .style("right",'20px')		
              .style("bottom",'20px')          
          }                                             
        })  
        .on("mouseout", function(event, d){   
          if(d.data.kd_eldat != '0'){
            var div = d3.select("#jakasembungbawagolok")
            div.remove()           
          }             
          d3.select(this.parentNode)
          .attr("transform", (d)=>{
            return `translate(${d.y0},${d.x0}), scale(1,1)`
          })                     
          d3.select(this.parentNode.childNodes[0])
          .attr("width", function(d){
            return rectWidth
          }) 
          d3.select(this.parentNode.childNodes[1].childNodes[1])
          .text(function(d){
            let jumlah = d.data.ur_eldat.length + d.data.Value.length
            if (jumlah < 26){
              if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
                return `${d.data.ur_eldat}} (Kode : ${d.data.kd_eldat})`                
              } else {
                return `${d.data.ur_eldat}}`                
              }
            } else {
              let copytext = d.data.ur_eldat
              let b = 26 - d.data.Value.length
              let text = copytext.slice(0,b-4) + "..."
              if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
                return `${text} (Kode : ${d.data.kd_eldat})`
              } else {
                return `${text}`                
              }
            }
          })                
        })
      .append("tspan")
      .attr("dy", "1.75em")
      .attr("x", function(d) {
        return 13;
      })
      .text(function(d) { 
        let jumlah = d.data.ur_eldat.length + d.data.Value.length
        if (jumlah < 26){
          if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
            return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})` 
          } else {
            return `${d.data.ur_eldat}`             
          }
        } else {
          let copytext = d.data.ur_eldat
          let b = 26 - d.data.Value.length
          let text = copytext.slice(0,b-4) + "..."
          if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
            return `${text} (Kode : ${d.data.kd_eldat})`
          } else {
            return `${text}`            
          }
        }
      })
      .style("fill", "white")
      .on("mouseover", function(event, d) {          
        d3.select(this.parentNode.parentNode)
        .attr("transform", (d)=>{
          return `translate(${d.y0},${d.x0}), scale(1.2,1.2)`
        })                     
        d3.select(this.parentNode.parentNode.childNodes[0])
        .attr("width", function(d){
          if(d.data.ur_eldat.length+d.data.Value.length < 19){
            return rectWidth
          } else {
            let sisa = (d.data.ur_eldat.length + d.data.Value.length + 2 - 19)*4.9
            let tambah = sisa + rectWidth
            return tambah
          } 
        }) 
        d3.select(this.parentNode.parentNode.childNodes[1].childNodes[1])
        .text(function(d){
          if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
            return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})`
          } else {
            return `${d.data.ur_eldat}`                        
          }
        })
        if(d.data.kd_eldat != "0"){
          const arrKL = d.data.KL  
          const arrIjin = d.data.ur_ijin 
          const arrDS = d.data.dok_syarat
          const arrDK = d.data.data_khusus
          const arrRekom = d.data.ur_rekom
          var div = d3.select("body").append("div")	
          .attr("class", "tooltip")
          .attr("id", "jakasembungbawagolok")				
          .style("opacity", 0)                                             
          div.transition()		
              .duration(200)		
              .style("opacity", 1);		
          div	.html("<b>K/L Pembina RKI :</b>"+ arrKL.map((x)=>{
            return(
              "<li>"+x+"</li>"
            )
          }).join('')+"<b>Ijin Final : </b>"+ arrIjin.map((y)=>{
            return(
              "<li>"+y+"</li>"
            )
          }).join('')+"<b>Rekom : </b>"+ arrRekom.map((z)=>{
            return(
              "<li>"+z+"</li>"
            )
          }).join('')+"<b>Dok Syarat : </b>"+ arrDS.map((a)=>{
            return(
              "<li>"+a+"</li>"
            )
          }).join('')+"<b>Data Khusus : </b>"+ arrDK.map((b)=>{
            return(
              "<li>"+b+"</li>"
            )
          }).join(''))
            .style("right",'20px')		
            .style("bottom",'20px')          
        }                                             
      })  
      .on("mouseout", function(event, d){   
        if(d.data.kd_eldat != '0'){
          var div = d3.select("#jakasembungbawagolok")
          div.remove()           
        }          
        d3.select(this.parentNode.parentNode)
        .attr("transform", (d)=>{
          return `translate(${d.y0},${d.x0}), scale(1,1)`
        })                     
        d3.select(this.parentNode.parentNode.childNodes[0])
        .attr("width", function(d){
          return rectWidth
        }) 
        d3.select(this.parentNode.parentNode.childNodes[1].childNodes[1])
        .text(function(d){
          let jumlah = d.data.ur_eldat.length + d.data.Value.length
          if (jumlah < 26){
            if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
              return `${d.data.ur_eldat} (Kode : ${d.data.kd_eldat})` 
            } else {
              return `${d.data.ur_eldat}`
            }
          } else {
            let copytext = d.data.ur_eldat
            let b = 26 - d.data.Value.length
            let text = copytext.slice(0,b-4) + "..."
            if(d.data.ur_grup === 'Kode HS' || d.data.ur_grup === 'Standar mutu'){
              return `${text} (Kode : ${d.data.kd_eldat})`             
            } else {
              return `${text}`
            }
          }
        })                
      })     
      // Enter any new nodes at the parent's previous position.
      // const nodeEnter = node
      //   .enter()
      //   .append("g")
      //   .attr("transform", (d) => `translate(${source.y0},${source.x0})`)
      //   .attr("fill-opacity", 0)
      //   .attr("stroke-opacity", 0)
      //   .on("click", (event, d) => {
      //     d.children = d.children ? null : d._children;
      //     update(d);
      //   })        

      // nodeEnter
      //   .append("circle")
      //   .attr("r", 2.5)
      //   .attr("fill", (d) => (d._children ? "#555" : "#999"))
      //   .attr("stroke-width", 10)   
        
      // var tooltip = d3.select('.tooltip-area')
      // .style('opacity', 0);                    
      // nodeEnter
      //   .append("text")
      //   .attr("dy", "0.31em")
      //   .attr("x", (d) => (d._children ? -6 : 6))
      //   .attr("text-anchor", (d) => (d._children ? "end" : "start"))
      //   .text((d) => d.data.ur_eldat)
      //   .raise()
      //   .on("mouseover", function(d) {
      //     // tooltip.style("opacity", 1);                  
      //     d3.select(this.parentNode).raise()
      //     .style("fill", "#02275d")
      //     .style("font", "15px sans-serif")                      
      //   })  
      //   .on("mouseout", function(){
      //     d3.select(this.parentNode).lower()
      //     .style("fill", "black") 
      //     .style("font", "10px sans-serif")
      //     // tooltip.style('opacity', 0);
      //   })                         
      //   .clone(true)
      //   .lower()
      //   .attr("stroke-linejoin", "round")
      //   .attr("stroke-width", 3)
      //   .attr("stroke", "white")            

      // Transition nodes to their new position.
      const nodeUpdate = node
        .merge(nodeEnter)
        .transition(transition)
        .attr("transform", (d) => `translate(${d.y},${d.x})`)
        .attr("fill-opacity", 1)
        .attr("stroke-opacity", 1);

      // Transition exiting nodes to the parent's new position.
      const nodeExit = node
        .exit()
        .transition(transition)
        .remove()
        .attr("transform", (d) => `translate(${source.y},${source.x})`)
        .attr("fill-opacity", 0)
        .attr("stroke-opacity", 0);

      // Update the links…
      const link = gLink.selectAll("path").data(links, (d) => d.target.id);

      // Enter any new links at the parent's previous position.
      const linkEnter = link
        .enter()
        .append("path")
        .attr("d", (d) => {
          const o = { x: source.x0, y: source.y0 };
          return diagonal({ source: o, target: o });
        });

      // Transition links to their new position.
      link.merge(linkEnter).transition(transition).attr("d", diagonal);

      // Transition exiting nodes to the parent's new position.
      link
        .exit()
        .transition(transition)
        .remove()
        .attr("d", (d) => {
          const o = { x: source.x, y: source.y };
          return diagonal({ source: o, target: o });
        });
      // Stash the old positions for transition.
      root.eachBefore((d) => {
        d.x0 = d.x;
        d.y0 = d.y;
      });
    }
    update(root);
  });
  return (
    <>
      {/* <select>
        <option Value="01">Beras</option>
        <option Value="02">Garam</option>
        <option Value="03">Ikan</option>
        <option Value="04">Daging Lembu</option>
        <option Value="05">Gula</option>
      </select> */}
      <CCard>
        <CCardBody>
          <h4>Dendrogram Referensi Neraca Komoditas</h4>
        </CCardBody>
      </CCard>     
      <CCard>
      <div className="ml-3 mt-3"><a style={{fontWeight:'500', fontSize:'16px'}}>Jika Error, harap geser ke samping</a></div>
        <CCardBody className="josss">
          <svg ref={svgRef}>         
          </svg>           
        </CCardBody>
      </CCard>       
    </>
  );
};

export default Dendogram;
